@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700;800;900&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "poppins", "sans-serif";
}

section {
  padding: 100px 200px;
}

.banner {
  position: relative;
  width: 100%;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  background: url(/reactportfolio/static/media/banner1.51f4df20.jpg);
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
}

.banner .content {
  max-width: 800px;
  -webkit-animation: moveright 1s linear 1;
          animation: moveright 1s linear 1;
  -webkit-animation-delay: 4s;
          animation-delay: 4s;
  visibility: hidden;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}


@-webkit-keyframes moveright {
  0%{
    -webkit-transform: translateX(-100px);
            transform: translateX(-100px);
    visibility: visible;
  }
  100%{
    -webkit-transform: translateX(0);
            transform: translateX(0);
    visibility: visible;
  }
  
}


@keyframes moveright {
  0%{
    -webkit-transform: translateX(-100px);
            transform: translateX(-100px);
    visibility: visible;
  }
  100%{
    -webkit-transform: translateX(0);
            transform: translateX(0);
    visibility: visible;
  }
  
}

.banner .content h2 {
  color: #fff;
  font-size: 1.8em;
  font-weight: 500;
}

.banner .content h2 span {
  font-size: 2.8em;
  font-weight: 600;
}

.animated-text {
  position: relative;
  height: 60px;
  overflow: hidden;
}

.animated-text h3 {
  color: #4e9eff;
  font-size: 2.5em;
  font-weight: 600;
  line-height: 70px;
  letter-spacing: 1px;
}

.animated-text h3:nth-child(1) {
  -webkit-animation: text-move 10s infinite;
          animation: text-move 10s infinite;
}

@-webkit-keyframes text-move {
  0% {
    margin-top: 0;
  }
  25% {
    margin-top: -70px;
  }
  50% {
    margin-top: -140px;
  }
  75% {
    margin-top: -70px;
  }
  100% {
    margin-top: 0;
  }
}

@keyframes text-move {
  0% {
    margin-top: 0;
  }
  25% {
    margin-top: -70px;
  }
  50% {
    margin-top: -140px;
  }
  75% {
    margin-top: -70px;
  }
  100% {
    margin-top: 0;
  }
}

.fab {
  
  transition: -webkit-transform 0.5s ease;
  
  transition: transform 0.5s ease;
  
  transition: transform 0.5s ease, -webkit-transform 0.5s ease;
  font-size: 28px;
  padding-right: 6px;
}

.fab:hover {
  -webkit-transform: translateY(-8px);
          transform: translateY(-8px);
}

