header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 40px 100px;
  z-index: 10000;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  transition: 0.5s;
  cursor: pointer;
  
}

header.sticky {
  background: #fff;
  padding: 10px 100px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.05);
}

header .logo {
  color: #fff;
  font-weight: 700;
  font-size: 1.8em;
  text-decoration: none;
  -webkit-animation: movedown 1s linear 1;
          animation: movedown 1s linear 1;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
  visibility: hidden;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  
}


@-webkit-keyframes movedown {
  0%{
    -webkit-transform: translateY(-100px);
            transform: translateY(-100px);
    visibility: visible;
  }
  100%{
    -webkit-transform: translateY(0);
            transform: translateY(0);
    visibility: visible;
  }
  
}


@keyframes movedown {
  0%{
    -webkit-transform: translateY(-100px);
            transform: translateY(-100px);
    visibility: visible;
  }
  100%{
    -webkit-transform: translateY(0);
            transform: translateY(0);
    visibility: visible;
  }
  
}

header.sticky .logo {
  color: #111;
}

header .logo span {
  color:#4e9eff;
}

header .navigation {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-animation: movedown 1s linear 1;
          animation: movedown 1s linear 1;
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
  visibility: hidden;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

header .navigation li {
  list-style: none;
  margin-left: 30px;
}

header .navigation li a {
  text-decoration: none;
  color: #fff;
  font-weight: 300;
}
header button {
  display: none;
}

header.sticky .navigation li a {
  color: #111;
}

header.sticky .navigation li a:hover {
  color: rgb(14, 162, 182);
}

header .navigation li a:hover {
  color: #4e9eff;
}

body {
  min-height: 110vh;
}
.title {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.section-title {
  position: relative;
  color: #4e9eff;
  font-size: 2.4em;
  font-weight: 600;
  margin-bottom: 60px;
}

.section-title:before {
  content: "";
  position: absolute;
  top: 56px;
  left: 50%;
  width: 140px;
  height: 4px;
  background: #4e9eff;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
}

.section-title::after {
  content: "";
  position: absolute;
  top: 50px;
  left: 50%;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background: #4e9eff;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
}

.about .content {
  position: relative;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin-top: 20px;
}

.about .content .col-left {
  position: relative;
  width: 45%;
}

.about .content .col-right {
  position: relative;
  width: 100%;
}

.about .content .col-left .img-card {
  position: relative;
  width: 100%;
  min-height: 450px;
}

.about .content .col-left .img-card img {
  top: 0;
  left: 0;
  width: 90%;
  height: 90%;
  object-fit: cover;
}
.skill {
  background-color: #030215;
}

.skill .content {
  position: relative;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  color: antiquewhite;
  margin-top: 20px;
}

.skill .content .col-left {
  position: relative;
  width: 46%;
}

.skill .content .col-right {
  position: relative;
  width: 46%;
  height: 40vh;
}

.skill .content .col-right .box {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 550px;
  height: 300px;
  background: transparent;
  border-bottom: 1px solid #fff;
  border-left: 1px solid #fff;
  display: -webkit-flex;
  display: flex;
}

.skill .content .col-right .box .skills {
  position: relative;
  -webkit-flex: 1 1;
          flex: 1 1;
  text-align: center;
}

.skill .content .col-right .box .skills .graph {
  position: absolute;
  width: 40px;
  background: cyan;
  bottom: 0;
  left: 50%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
}

.skill .content .col-right .box .skills .graph:before {
  content: "";
  position: absolute;
  top: 2px;
  left: 2px;
  right: 2px;
  bottom: 0;
  background: linear-gradient(0deg, #003ba5, #009ffe);
}

.skill .content .col-right .box .skills .graph:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 50%;
  height: 100%;
  background: rgba(255, 255, 255, 0.1);
}

.skill .content .col-right .box .skills .graph .percent {
  position: absolute;
  top: -20px;
  left: 50%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
  text-align: center;
  color: #fff;
  font-weight: bold;
}

.box .skills .name {
  position: absolute;
  bottom: -32px;
  left: 50%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
  text-align: center;
  color: #fff;
  background: #001e8a;
  padding: 2px 6px;
  border-radius: 4px;
}
.project .item {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  max-width: 800px;
  margin: 0 auto;
  margin-bottom: 10%;
}

.project .item:nth-child(even) {
  -webkit-flex-direction: row-reverse;
          flex-direction: row-reverse;
}

.project .item .left {
  -webkit-flex: 1 1;
          flex: 1 1;
}

.project .item .right {
  -webkit-flex: 1 1;
          flex: 1 1;
  margin-left: -150px;
  background-color: #fff;
  padding: 2rem 2rem;
  border-radius: 12px;
  box-shadow: 0px 0px 20px #00000028;
  z-index: 2;
}

.project .item:nth-child(even) .right {
  margin-left: 0;
}

.project .item:nth-child(even) .left {
  margin-left: -150px;
}

.project .item .left .img {
  position: relative;
  height: 500px;
  overflow: hidden;
  border-radius: 12px;
  box-shadow: 0px 0px 20px #4705fc33;
}

.project .item .left .img:after {
  position: absolute;
  content: "";
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  background-color: #5a24ed38;
}

.project .item .left .img img {
  width: 100%;
  height: 500px;
  object-fit: cover;
  transition: 0.3s ease transform;
}

.project .item:hover .left .img img {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  z-index: 1;
  background-color: #5a24ed38;
}

.project .item .right .project-title {
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.project .item .right .project-sub-title {
  font-size: 1rem;
  margin-bottom: 2rem;
}

.project .item .right .project-desc {
  margin-bottom: 3rem;
}

